<template>
    <page flex>
        <flex-column>
            <flex-column-header>
                <page-header type="account">
                    <template v-slot:left>
                        <h3>Change Account</h3>
                    </template>
                    <template v-slot:right>
                        <div class="search" :class="{active:searchFocussed || search.length}">
                            <input v-model="search" @focus="searchFocussed = true" @blur="searchFocussed = false" placeholder="Search by keyword" />
                            <div class="search-icon" @click="search = ''">
                                <fluro-icon icon="times" v-if="search.length" />
                                <fluro-icon icon="search" v-else />
                            </div>
                        </div>
                    </template>
                </page-header>
            </flex-column-header>
            <flex-column>
                <!-- <pre>{{items.length}}</pre> -->
                <fluro-data-table :enableSelection="false" :allDefinitions="true" :clicked="rowClicked" :search="search" :items="items" :init-page="$route.query.page" :filter-config="filterConfig" :init-sort="{sortKey:$route.query.sortKey, sortDirection:$route.query.sortDirection, sortType:$route.query.sortType}" @raw="rowsChanged" @page="pageChanged" @sort="sortChanged" :columns="columns" />
            </flex-column>
        </flex-column>
    </page>
</template>
<script>
/////////////////////////////////////////

//Mixin SEO and head
import SEOMixin from '@/mixins/SEOMixin';
import PersistRouteMixin from '@/mixins/PersistRouteMixin';
import UserMixin from '@/mixins/UserMixin';
import BreadcrumbMixin from '@/mixins/BreadcrumbMixin';

//Get our components from FluroVue
import { Layout, FluroContentForm, FluroContentFormField } from 'fluro-vue-ui';
import FluroDataTable from '@/components/table/Table.vue';
import FilterConditionGroup from '@/components/FilterConditionGroup.vue';


import FilterService from '@/modules/FilterService';

/////////////////////////////////////////

import Vue from "vue";

/////////////////////////////////////////




let DateCell = Vue.extend({
    props: {
        'row': {
            type: Object,
        },
        'column': {
            type: Object,
        },
        'data': {
            // type: Object,
        },
    },
    template: `<span class="inline-tag" :content="data | timeago" v-tippy>{{data | formatDate('MMM D, YYYY')}}</span>`,
    /**
    template: `
        <v-tooltip bottom>
            <template v-slot:activator="{ on }">-->
                <span class="inline-tag" v-on="on">{{data | formatDate('MMM D, YYYY')}}</span>
            </template>
            <span>{{data | timeago}}</span>
        </v-tooltip>
      `,
      /**/

});

let TitleCell = Vue.extend({
    props: {
        'row': {
            type: Object,
        },
        'column': {
            type: Object,
        },
        'data': {
            // type: Object,
        },
    },
    template: `
    <div>
     <fluro-status-label right v-if="row.status != 'active'" :value="row.status"/>
        <strong>{{row.title}}</strong><br/>
       
    </div>`,
});


/////////////////////////////////////////////////////

var viewModes = [

    //List/Default View
    {
        title: 'List',
        value: 'default',
        icon: 'bars',
    },


    {
        title: 'Map',
        value: 'map',
        icon: 'map-marked',
    },

    {
        title: 'Duplicates',
        value: 'duplicates',
        icon: 'users-class',
    },

    {
        title: 'Checkins',
        value: 'checkins',
        icon: 'sign-in',
    },


];


/**
var defaultFilters = {
    operator: 'and',
    filters: [{
            operator: 'and',
            filters: [{
                    key: 'status',
                    comparator: '==',
                    value: 'active',
                },
                {
                    key: 'age',
                    comparator: '>',
                    value: 18,
                }
            ]
        },
        {
            operator: 'or',
            filters: [{
                    key: 'status',
                    comparator: '==',
                    value: 'active',
                },
                {
                    key: 'age',
                    comparator: '>',
                    value: 18,
                }
            ]
        },
    ]
}
/**/

var defaultFilters = {
    // operator: 'and',
    // filters: [{
    //     operator: 'and',
    //     filters: [{
    //         key: 'status',
    //         comparator: '==',
    //         value: 'active',
    //     }]
    // }]
}




/////////////////////////////////////////////////////

export default {
    props: {
        startPage: {
            type: Number,
            default: 1,
        },
    },
    components: {
        FluroDataTable,
        FilterConditionGroup,

        // RealmDotCell,
    },
    mixins: [PersistRouteMixin, SEOMixin, BreadcrumbMixin, UserMixin, Layout],
    created() {
        // console.log('LIST CREATED')
    },
    destroyed() {
        // console.log('LIST DESTROYED');
    },
    data() {

        var initalFilter = defaultFilters;

        if (this.$route.query.filter && this.$route.query.filter.length) {
            initalFilter = JSON.parse(this.$route.query.filter);
        }


        return {
            rows: [], //this is so we can feed the subset into the filter configuration
            showFilters: false,
            // async:true,
            loading: true,
            filterConfig: initalFilter,
            viewModes: viewModes,
            viewMode: viewModes[0],
            selection: this.$selection,
            search: '',
            searchFocussed: false,
            columns: [
                { title: 'Title', key: 'title', renderer: TitleCell, },
                { type: 'date', title: 'Updated', shrink: true, key: 'updated', renderer: DateCell, sortType: 'date' },
                { type: 'date', title: 'Created', shrink: true, key: 'created', renderer: DateCell, sortType: 'date' },
                { title: 'Status', key: 'status' },
            ],
        }
    },
    computed: {
        columnKeys() {
            return _.map(this.columns, 'key');
        },
        filterDebounce() {
            if (this.$vuetify.breakpoint.smAndDown) {
                return 1500;
            } else {
                return 700
            }
        },
        filterChangeString() {
            return FilterService.getFilterChangeString(this.filterConfig);
        }
    },
    watch: {
        filterChangeString() {
            console.log('Filters changed', this.filterConfig);
            this.$router.replace({ query: Object.assign({}, this.$route.query, { filter: JSON.stringify(this.filterConfig) }) });
        }
    },
    methods: {
        changeAccount(account) {

            var self = this;

            var currentAccountID = _.get(self, 'user.account._id');
            if (currentAccountID == account._id) {
                return complete();
            }

            //////////////////////////////////

            var promise = self.$fluro.auth.changeAccount(account);

            //////////////////////////////////

            promise.then(complete, function(err) {

                console.log('ERROR', err)
                var message = self.$fluro.utils.errorMessage(err);
            })

            //////////////////////////////////

            function complete() {

                console.log('Switched into', account.title);

                //Go to the home page
                self.$router.push({
                    name: 'home',
                })
            }
        },

        rowClicked(row, column) {
            // console.log('ROW', row, column);
            var self = this;

            self.changeAccount(row);
        },
        rowsChanged(rows) {
            this.rows = rows;
        },
        pageChanged(pageNumber) {

            // if(this.$route.query.page == pageNumber) {
            //     console.log('Page is already set', pageNumber)
            //     return;
            // }
            // console.log('Table page changed', pageNumber);
            this.$router.push({ query: Object.assign({}, this.$route.query, { page: pageNumber }) });
        },
        sortChanged(sortingDetails) {

            //console.log('Table sort changed', sortingDetails);
            this.$router.replace({ query: Object.assign({}, this.$route.query, sortingDetails) });
        },
    },
    // activated() {
    //     console.log('ACTIVATED')
    // },
    // deactivated() {
    //      console.log('DEACTIVATED')
    // },
    asyncComputed: {
        // filterableFields: {
        //     default: [],
        //     get() {

        //         var self = this;

        //         return new Promise(function(resolve, reject) {

        //             self.$fluro.types.get('contact').then(function(res) {

        //                 return resolve(res);
        //             }, reject);

        //         });
        //     }
        // }
        items: {
            default: [],
            get() {

                var self = this;
                self.loading = true;

                return new Promise(function(resolve, reject) {
                    return self.$fluro.api.get('/user/accounts').then(function(res) {
                        resolve(res.data);
                        self.loading = false;
                    }, reject);
                });

            }
        },
    }
}
</script>
