    position: relative;

    input {
        border: 1px solid #f5f5f5;
        width: 100%;
        background: #f5f5f5; //#eee;

        border-radius: 2px;
        height: 30px;
        line-height: 30px;
        padding: 0 5px;
        display: inline-block;

        &:focus {
            outline: none;
            border: 1px solid #ddd;
        }
    }

    .search-icon {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        line-height: 34px;
        height: 30px;
        width: 30px;
        text-align: center;
